'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useParams, useRouter } from 'next/navigation';
import { StarIcon, ClockIcon, ArrowLeftIcon, ShieldCheckIcon, UserCircleIcon } from '@heroicons/react/24/outline';
import { StarIcon as StarIconSolid } from '@heroicons/react/24/solid';
import { useAuth } from '@/contexts/AuthContext';

export default function ServiceDetails() {
  const { id } = useParams();
  const router = useRouter();
  const { user, isAuthenticated } = useAuth();
  const [service, setService] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('description');
  const [requestFormData, setRequestFormData] = useState({
    description: '',
    additionalFiles: false,
    deadline: '3',
  });
  const [formErrors, setFormErrors] = useState({
    description: '',
  });

  // محاكاة جلب بيانات الخدمة من API
  useEffect(() => {
    const fetchService = async () => {
      setLoading(true);
      try {
        // في بيئة حقيقية سنستدعي API
        // const response = await fetch(`/api/services/${id}`);
        // const data = await response.json();
        
        // محاكاة بيانات الخدمة
        setTimeout(() => {
          const mockService = {
            id: id,
            title: 'توليد صور احترافية باستخدام Midjourney',
            description: 'إنشاء صور عالية الجودة باستخدام تقنية Midjourney مع تخصيص كامل حسب طلبك. أقدم خدمة احترافية لإنشاء صور فريدة ومذهلة تناسب مختلف الاستخدامات التسويقية والإبداعية والتجارية.',
            longDescription: `
            # خدمة إنشاء صور احترافية باستخدام Midjourney

            تقدم هذه الخدمة تجربة متكاملة لإنشاء صور عالية الجودة باستخدام تقنية Midjourney المتطورة، مع الاهتمام بأدق التفاصيل وتلبية احتياجاتك بدقة.

            ## ما الذي تحصل عليه:

            - صور عالية الدقة بجودة استثنائية (حتى 4096×4096 بكسل)
            - تعديلات غير محدودة حتى الوصول للنتيجة المطلوبة
            - ملكية كاملة للصور المنتجة
            - دعم لمختلف الأساليب الفنية والأنماط البصرية
            - توجيه احترافي لكتابة الوصف (Prompt) المثالي
            - تسليم سريع في غضون 24 ساعة (حسب التعقيد)

            ## مجالات استخدام الصور:

            - المحتوى التسويقي والإعلاني
            - وسائل التواصل الاجتماعي
            - تصميم واجهات المواقع والتطبيقات
            - الكتب والمنشورات الرقمية
            - العروض التقديمية والتقارير

            ## كيفية العمل:

            1. أخبرني بالتفصيل عن نوع الصورة التي ترغب في إنشائها
            2. أقوم بكتابة ومعايرة وصف (Prompt) احترافي
            3. إنشاء نماذج أولية للاختيار
            4. تعديل وتحسين حسب ملاحظاتك
            5. تسليم الصور النهائية بالجودة والحجم المطلوبين

            ## خبرتي:

            أعمل في مجال توليد الصور بالذكاء الاصطناعي منذ أكثر من عامين، مع خبرة واسعة في Midjourney وتقنيات الـ Prompt Engineering. قمت بتنفيذ أكثر من 500 مشروع ناجح لعملاء من مختلف المجالات.
            `,
            image: '/images/services/image-generation.jpg',
            category: 'image-generation',
            price: 150,
            rating: 4.9,
            reviewsCount: 127,
            deliveryTime: '24 ساعة',
            provider: {
              id: 'provider-1',
              name: 'أحمد محمد',
              image: '/images/providers/provider1.jpg',
              rating: 4.9,
              reviewsCount: 342,
              memberSince: 'مارس 2022',
              responseTime: '2 ساعة',
              skills: ['Midjourney', 'DALL-E', 'Stable Diffusion', 'Photoshop'],
              completedProjects: 516,
              bio: 'مصمم محترف متخصص في توليد الصور باستخدام الذكاء الاصطناعي، مع خبرة واسعة في تقنيات Midjourney وأدوات التصميم المختلفة.'
            },
            features: [
              'صور بدقة عالية تصل إلى 4K',
              'تعديلات غير محدودة حتى الرضا التام',
              'دعم لمختلف أساليب الفن والتصوير',
              'تسليم بصيغ متعددة (JPG, PNG, WEBP)',
              'ملكية كاملة للصور المنتجة'
            ],
            requirements: [
              'وصف تفصيلي للصورة المطلوبة',
              'أمثلة أو مراجع للأسلوب المفضل (اختياري)',
              'تحديد الغرض من الصورة',
              'ذكر أي عناصر يجب تجنبها'
            ],
            reviews: [
              {
                id: 'review-1',
                user: 'سارة الأحمد',
                rating: 5,
                date: 'منذ 3 أيام',
                content: 'تجربة رائعة! الصور كانت بجودة استثنائية وتم التسليم قبل الموعد المحدد. سأتعامل معه مرة أخرى بالتأكيد.',
                image: '/images/gallery/review1.jpg'
              },
              {
                id: 'review-2',
                user: 'خالد محمود',
                rating: 5,
                date: 'منذ أسبوع',
                content: 'مبدع جداً وسريع الاستجابة للتعديلات. النتائج فاقت توقعاتي وساعدتني كثيراً في حملتي الإعلانية.',
                image: '/images/gallery/review2.jpg'
              },
              {
                id: 'review-3',
                user: 'ليلى عمر',
                rating: 4,
                date: 'منذ 2 أسبوع',
                content: 'الصور كانت جميلة جداً، ولكن استغرق الأمر وقتاً أطول قليلاً مما كنت أتوقع. مع ذلك، النتيجة النهائية كانت ممتازة.',
                image: null
              }
            ],
            faq: [
              {
                question: 'هل يمكنني طلب تعديلات بعد استلام الصور؟',
                answer: 'نعم، الخدمة تشمل تعديلات غير محدودة حتى تكون راضياً تماماً عن النتيجة النهائية.'
              },
              {
                question: 'ما هي حقوق الملكية للصور المنتجة؟',
                answer: 'جميع الصور المنتجة تعود ملكيتها الكاملة لك، ويمكنك استخدامها في أي غرض تجاري أو شخصي.'
              },
              {
                question: 'هل يمكنني طلب صور تحتوي على أشخاص محددين؟',
                answer: 'نعم، يمكن إنشاء صور تحاكي أشخاصاً بشرط الالتزام بسياسات الخصوصية وعدم انتهاك حقوق الصورة للأشخاص الحقيقيين.'
              },
              {
                question: 'ما هي صيغ الملفات المتاحة للتسليم؟',
                answer: 'يتم تسليم الصور بصيغ JPG و PNG و WEBP حسب الطلب، مع إمكانية توفير ملفات PSD للتعديل (بتكلفة إضافية).'
              }
            ],
            relatedServices: [
              {
                id: 'service-6',
                title: 'إنشاء قصص متحركة بالذكاء الاصطناعي',
                image: '/images/services/animated-story.jpg',
                price: 280
              },
              {
                id: 'service-2',
                title: 'إنتاج فيديوهات قصيرة بالذكاء الاصطناعي',
                image: '/images/services/video-generation.jpg',
                price: 350
              },
              {
                id: 'service-10',
                title: 'تطوير سير عمل مخصصة باستخدام Comfy UI',
                image: '/images/services/comfy-ui.jpg',
                price: 250
              }
            ]
          };
          setService(mockService);
          setLoading(false);
        }, 1000);
      } catch (error) {
        console.error('Error fetching service:', error);
        setLoading(false);
      }
    };
    
    if (id) {
      fetchService();
    }
  }, [id]);

  // معالجة تقديم نموذج الطلب
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    // التحقق من صحة البيانات
    let errors = {
      description: '',
    };
    
    if (!requestFormData.description.trim()) {
      errors.description = 'الرجاء إدخال وصف لطلبك';
    }
    
    if (errors.description) {
      setFormErrors(errors);
      return;
    }
    
    // التحقق من تسجيل الدخول
    if (!isAuthenticated) {
      router.push(`/login?redirect=/services/${id}`);
      return;
    }
    
    // إرسال الطلب (محاكاة)
    alert('تم إرسال طلبك بنجاح!');
    // في بيئة حقيقية سنرسل الطلب إلى API
    // router.push('/dashboard');
  };

  // تغيير بيانات النموذج
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setRequestFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
    
    // مسح رسائل الخطأ عند الكتابة
    if (formErrors[name as keyof typeof formErrors]) {
      setFormErrors((prev) => ({
        ...prev,
        [name]: '',
      }));
    }
  };

  // عرض التقييم بالنجوم
  const renderStars = (rating: number) => {
    return (
      <div className="flex items-center">
        {[...Array(5)].map((_, i) => (
          i < Math.floor(rating) ? (
            <StarIconSolid key={i} className="h-5 w-5 text-yellow-400" />
          ) : (
            <StarIcon key={i} className="h-5 w-5 text-gray-300" />
          )
        ))}
        <span className="text-gray-600 mr-1">{rating.toFixed(1)}</span>
      </div>
    );
  };

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="flex justify-center items-center py-16">
          <div className="inline-block h-8 w-8 animate-spin rounded-full border-4 border-solid border-indigo-500 border-r-transparent"></div>
          <span className="mr-4 text-gray-600">جاري تحميل بيانات الخدمة...</span>
        </div>
      </div>
    );
  }

  if (!service) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="text-center py-16">
          <h2 className="text-2xl font-bold text-gray-900 mb-4">الخدمة غير موجودة</h2>
          <p className="text-gray-600 mb-6">عذراً، لم يتم العثور على الخدمة المطلوبة.</p>
          <Link
            href="/services"
            className="inline-flex items-center px-4 py-2 bg-indigo-600 hover:bg-indigo-700 text-white text-sm font-medium rounded-md shadow-sm transition-colors"
          >
            العودة إلى الخدمات
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* التنقل */}
        <nav className="flex mb-6" aria-label="Breadcrumb">
          <ol className="inline-flex items-center space-x-1 md:space-x-3 space-x-reverse rtl:space-x-reverse">
            <li className="inline-flex items-center">
              <Link 
                href="/" 
                className="inline-flex items-center text-sm font-medium text-gray-700 hover:text-indigo-600"
              >
                الرئيسية
              </Link>
            </li>
            <li>
              <div className="flex items-center">
                <ArrowLeftIcon className="w-3 h-3 text-gray-400 mx-1" />
                <Link 
                  href="/services" 
                  className="text-sm font-medium text-gray-700 hover:text-indigo-600 mr-1 md:mr-2"
                >
                  الخدمات
                </Link>
              </div>
            </li>
            <li>
              <div className="flex items-center">
                <ArrowLeftIcon className="w-3 h-3 text-gray-400 mx-1" />
                <span className="mr-1 text-sm font-medium text-gray-500 md:mr-2">{service.title}</span>
              </div>
            </li>
          </ol>
        </nav>

        {/* معلومات الخدمة الرئيسية */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* القسم الرئيسي */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-lg overflow-hidden shadow-sm border border-gray-200">
              {/* صورة الخدمة */}
              <div className="relative aspect-video">
                <div className="absolute inset-0 bg-gradient-to-b from-indigo-900 to-purple-900"></div>
                <Image
                  src={service.image}
                  alt={service.title}
                  layout="fill"
                  objectFit="cover"
                  className="z-0"
                  onError={(e) => {
                    const target = e.target as HTMLImageElement;
                    target.style.display = 'none';
                  }}
                />
              </div>

              {/* تفاصيل الخدمة */}
              <div className="p-6">
                <h1 className="text-2xl font-bold text-gray-900 mb-2">{service.title}</h1>
                
                <div className="flex items-center mb-4 flex-wrap gap-2">
                  <span className="bg-indigo-100 text-indigo-800 text-xs font-medium px-2.5 py-0.5 rounded-full">
                    {service.category}
                  </span>
                  <div className="flex items-center">
                    {renderStars(service.rating)}
                    <span className="text-sm text-gray-500 mr-1">({service.reviewsCount} تقييم)</span>
                  </div>
                  <span className="flex items-center text-sm text-gray-500">
                    <ClockIcon className="h-4 w-4 ml-1" />
                    وقت التسليم: {service.deliveryTime}
                  </span>
                </div>

                {/* شريط التنقل */}
                <div className="border-b border-gray-200 mb-6">
                  <nav className="flex -mb-px space-x-6 space-x-reverse">
                    {['description', 'requirements', 'provider', 'reviews', 'faq'].map((tab) => (
                      <button
                        key={tab}
                        onClick={() => setActiveTab(tab)}
                        className={`text-sm font-medium py-4 px-1 border-b-2 ${
                          activeTab === tab
                            ? 'border-indigo-500 text-indigo-600'
                            : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                        }`}
                      >
                        {tab === 'description' && 'الوصف'}
                        {tab === 'requirements' && 'المتطلبات'}
                        {tab === 'provider' && 'مزود الخدمة'}
                        {tab === 'reviews' && 'التقييمات'}
                        {tab === 'faq' && 'الأسئلة الشائعة'}
                      </button>
                    ))}
                  </nav>
                </div>

                {/* محتوى التبويب النشط */}
                <div>
                  {/* الوصف */}
                  {activeTab === 'description' && (
                    <div>
                      <p className="text-gray-700 mb-6">{service.description}</p>
                      <div className="prose prose-indigo max-w-none">
                        <div dangerouslySetInnerHTML={{ __html: service.longDescription.replace(/\n/g, '<br />') }} />
                      </div>
                      
                      <div className="mt-8">
                        <h3 className="text-lg font-medium text-gray-900 mb-4">المميزات</h3>
                        <ul className="space-y-2">
                          {service.features.map((feature: string, idx: number) => (
                            <li key={idx} className="flex items-start">
                              <CheckIcon className="h-5 w-5 text-green-500 flex-shrink-0 ml-1.5" />
                              <span className="text-gray-700">{feature}</span>
                            </li>
                          ))}
                        </ul>
                      </div>
                    </div>
                  )}
                  
                  {/* المتطلبات */}
                  {activeTab === 'requirements' && (
                    <div>
                      <h3 className="text-lg font-medium text-gray-900 mb-4">ما تحتاج توفيره لطلب الخدمة</h3>
                      <ul className="space-y-3">
                        {service.requirements.map((req: string, idx: number) => (
                          <li key={idx} className="flex items-start">
                            <span className="flex-shrink-0 h-5 w-5 ml-1.5 bg-indigo-100 text-indigo-800 rounded-full text-xs flex items-center justify-center">
                              {idx + 1}
                            </span>
                            <span className="text-gray-700">{req}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                  
                  {/* مزود الخدمة */}
                  {activeTab === 'provider' && (
                    <div>
                      <div className="flex items-start">
                        <div className="relative h-16 w-16 rounded-full overflow-hidden ml-4">
                          <div className="absolute inset-0 bg-indigo-200"></div>
                          <Image
                            src={service.provider.image}
                            alt={service.provider.name}
                            layout="fill"
                            objectFit="cover"
                            className="z-0"
                            onError={(e) => {
                              const target = e.target as HTMLImageElement;
                              target.style.display = 'none';
                              (e.target as HTMLElement).parentElement!.classList.add('flex', 'items-center', 'justify-center');
                              const textElement = document.createElement('span');
                              textElement.textContent = service.provider.name.charAt(0);
                              textElement.classList.add('text-indigo-600', 'font-semibold', 'text-xl');
                              (e.target as HTMLElement).parentElement!.appendChild(textElement);
                            }}
                          />
                        </div>
                        <div>
                          <h3 className="text-lg font-medium text-gray-900">{service.provider.name}</h3>
                          <div className="flex items-center mt-1">
                            {renderStars(service.provider.rating)}
                            <span className="text-sm text-gray-500 mr-1">({service.provider.reviewsCount} تقييم)</span>
                          </div>
                          <p className="text-sm text-gray-500 mt-1">عضو منذ {service.provider.memberSince}</p>
                        </div>
                      </div>
                      
                      <div className="mt-6">
                        <p className="text-gray-700 mb-4">{service.provider.bio}</p>
                        
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-6">
                          <div className="bg-gray-50 p-4 rounded-lg">
                            <h4 className="text-sm font-medium text-gray-900 mb-2">المهارات</h4>
                            <div className="flex flex-wrap gap-2">
                              {service.provider.skills.map((skill: string, idx: number) => (
                                <span key={idx} className="bg-white text-xs font-medium px-2.5 py-1 rounded border border-gray-200">
                                  {skill}
                                </span>
                              ))}
                            </div>
                          </div>
                          
                          <div className="bg-gray-50 p-4 rounded-lg">
                            <h4 className="text-sm font-medium text-gray-900 mb-2">إحصائيات</h4>
                            <div className="space-y-2">
                              <div className="flex justify-between">
                                <span className="text-gray-600 text-sm">المشاريع المكتملة</span>
                                <span className="font-medium text-sm">{service.provider.completedProjects}</span>
                              </div>
                              <div className="flex justify-between">
                                <span className="text-gray-600 text-sm">وقت الرد</span>
                                <span className="font-medium text-sm">{service.provider.responseTime}</span>
                              </div>
                            </div>
                          </div>
                        </div>
                        
                        <div className="text-center mt-6">
                          <Link 
                            href={`/provider/${service.provider.id}`}
                            className="inline-flex items-center px-4 py-2 border border-indigo-600 text-indigo-600 hover:bg-indigo-50 text-sm font-medium rounded-md"
                          >
                            عرض الملف الشخصي
                          </Link>
                        </div>
                      </div>
                    </div>
                  )}
                  
                  {/* التقييمات */}
                  {activeTab === 'reviews' && (
                    <div>
                      <div className="flex items-center justify-between mb-6">
                        <h3 className="text-lg font-medium text-gray-900">التقييمات والمراجعات</h3>
                        <div className="flex items-center">
                          <span className="text-3xl font-bold text-gray-900 ml-2">{service.rating}</span>
                          {renderStars(service.rating)}
                          <span className="text-sm text-gray-500 mr-2">({service.reviewsCount})</span>
                        </div>
                      </div>
                      
                      <div className="space-y-6">
                        {service.reviews.map((review: any) => (
                          <div key={review.id} className="border-b border-gray-200 pb-6 last:border-b-0">
                            <div className="flex items-center justify-between mb-2">
                              <div className="flex items-center">
                                <div className="h-10 w-10 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-800 font-medium ml-3">
                                  {review.user.charAt(0)}
                                </div>
                                <div>
                                  <h4 className="font-medium text-gray-900">{review.user}</h4>
                                  <div className="flex items-center text-sm text-gray-500">
                                    {renderStars(review.rating)}
                                    <span className="mr-2">{review.date}</span>
                                  </div>
                                </div>
                              </div>
                            </div>
                            <p className="text-gray-700 mt-2">{review.content}</p>
                            {review.image && (
                              <div className="mt-3">
                                <div className="relative h-32 w-32 rounded-md overflow-hidden">
                                  <Image
                                    src={review.image}
                                    alt="صورة المراجعة"
                                    layout="fill"
                                    objectFit="cover"
                                    className="z-0"
                                    onError={(e) => {
                                      const target = e.target as HTMLImageElement;
                                      target.style.display = 'none';
                                    }}
                                  />
                                </div>
                              </div>
                            )}
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                  
                  {/* الأسئلة الشائعة */}
                  {activeTab === 'faq' && (
                    <div>
                      <h3 className="text-lg font-medium text-gray-900 mb-4">الأسئلة الشائعة</h3>
                      <div className="space-y-4">
                        {service.faq.map((item: any, idx: number) => (
                          <div key={idx} className="border border-gray-200 rounded-lg">
                            <details className="group">
                              <summary className="flex justify-between items-center font-medium cursor-pointer list-none p-4">
                                <span className="text-gray-900">{item.question}</span>
                                <span className="transition group-open:rotate-180">
                                  <svg fill="none" height="24" width="24" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" viewBox="0 0 24 24">
                                    <polyline points="6 9 12 15 18 9"></polyline>
                                  </svg>
                                </span>
                              </summary>
                              <div className="px-4 pb-4 pt-0 text-gray-700">
                                {item.answer}
                              </div>
                            </details>
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>
            
            {/* الخدمات ذات الصلة */}
            <div className="mt-8">
              <h2 className="text-xl font-bold text-gray-900 mb-4">خدمات ذات صلة</h2>
              <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                {service.relatedServices.map((relatedService: any) => (
                  <Link
                    key={relatedService.id}
                    href={`/services/${relatedService.id}`}
                    className="group block bg-white rounded-lg overflow-hidden shadow-sm border border-gray-200 hover:shadow-md transition-shadow"
                  >
                    <div className="relative aspect-video">
                      <div className="absolute inset-0 bg-indigo-900"></div>
                      <Image
                        src={relatedService.image}
                        alt={relatedService.title}
                        layout="fill"
                        objectFit="cover"
                        className="z-0 group-hover:scale-105 transition-transform duration-300"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.style.display = 'none';
                        }}
                      />
                    </div>
                    <div className="p-4">
                      <h3 className="font-medium text-gray-900 mb-1 line-clamp-1 group-hover:text-indigo-600 transition-colors">
                        {relatedService.title}
                      </h3>
                      <div className="flex justify-between items-center">
                        <span className="text-indigo-600 font-bold">{relatedService.price} ج.م</span>
                        <span className="text-sm text-gray-500">عرض التفاصيل</span>
                      </div>
                    </div>
                  </Link>
                ))}
              </div>
            </div>
          </div>
          
          {/* قسم جانبي - طلب الخدمة */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 sticky top-6">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-bold text-gray-900">السعر</h2>
                <span className="text-2xl font-bold text-indigo-600">{service.price} ج.م</span>
              </div>
              
              <div className="border-t border-gray-200 my-4 pt-4">
                <div className="flex items-center mb-3">
                  <ClockIcon className="h-5 w-5 text-gray-400 ml-2" />
                  <span className="text-sm text-gray-700">وقت التسليم: {service.deliveryTime}</span>
                </div>
                <div className="flex items-center mb-3">
                  <ShieldCheckIcon className="h-5 w-5 text-gray-400 ml-2" />
                  <span className="text-sm text-gray-700">ضمان استرداد الأموال</span>
                </div>
                <div className="flex items-center">
                  <UserCircleIcon className="h-5 w-5 text-gray-400 ml-2" />
                  <span className="text-sm text-gray-700">خدمة عملاء متاحة 24/7</span>
                </div>
              </div>
              
              <form onSubmit={handleSubmit} className="mt-6">
                <div className="mb-4">
                  <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
                    وصف مشروعك بالتفصيل <span className="text-red-500">*</span>
                  </label>
                  <textarea
                    id="description"
                    name="description"
                    rows={5}
                    value={requestFormData.description}
                    onChange={handleChange}
                    className={`block w-full rounded-md ${
                      formErrors.description ? 'border-red-300 focus:border-red-500 focus:ring-red-500' : 'border-gray-300 focus:border-indigo-500 focus:ring-indigo-500'
                    }`}
                    placeholder="أخبرنا بالتفصيل عما تحتاجه..."
                  ></textarea>
                  {formErrors.description && (
                    <p className="mt-1 text-sm text-red-600">{formErrors.description}</p>
                  )}
                </div>
                
                <div className="mb-4">
                  <label htmlFor="deadline" className="block text-sm font-medium text-gray-700 mb-1">
                    المدة المتوقعة للتنفيذ
                  </label>
                  <select
                    id="deadline"
                    name="deadline"
                    value={requestFormData.deadline}
                    onChange={handleChange}
                    className="block w-full rounded-md border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  >
                    <option value="3">3 أيام</option>
                    <option value="7">7 أيام</option>
                    <option value="14">14 يوم</option>
                    <option value="30">30 يوم</option>
                  </select>
                </div>
                
                <div className="mb-6">
                  <div className="flex items-center">
                    <input
                      id="additionalFiles"
                      name="additionalFiles"
                      type="checkbox"
                      checked={requestFormData.additionalFiles}
                      onChange={(e) => setRequestFormData((prev) => ({
                        ...prev,
                        additionalFiles: e.target.checked,
                      }))}
                      className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500 ml-2"
                    />
                    <label htmlFor="additionalFiles" className="text-sm text-gray-700">
                      لدي ملفات إضافية سأرسلها لاحقًا
                    </label>
                  </div>
                </div>
                
                <button
                  type="submit"
                  className="w-full bg-indigo-600 hover:bg-indigo-700 text-white py-3 px-4 rounded-md font-medium transition-colors"
                >
                  طلب الخدمة الآن
                </button>
                
                {!isAuthenticated && (
                  <p className="mt-2 text-xs text-gray-500 text-center">
                    سيتم نقلك إلى صفحة تسجيل الدخول أولًا
                  </p>
                )}
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 